<template>
  <van-row class="dt-main">
    <!-- 顶部蓝色框首页 -->
    <van-row class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">基本信息</p>
      </van-col>
    </van-row>
    <van-row class="dt-content">
      <userInformation :information="information"
                       :department="department"></userInformation>
      <el-button style="width:80%;"
                 type="primary"
                 @click="showPop=true">编辑信息</el-button>
    </van-row>
    <van-popup v-model="showPop"
               closeable
               position="bottom"
               :style="{ height: '80%' }">
      <div class="pop-box">
        <van-form validate-first
                  :submit-on-enter="false"
                  @submit="updateInfo">
          <van-divider :style="{ fontSize: '18px', color: '#1989fa', borderColor: '#1989fa', padding: '0 16px'}">修改个人信息</van-divider>
          <van-field v-model="upForm.real_name"
                     name="real_name"
                     label="真实姓名"
                     placeholder="请输入真实姓名"
                     :rules="[{ required: true, message: '请输入真实姓名' }]" />
          <van-field v-model="upForm.username"
                     name="username"
                     label="用户名"
                     placeholder="请输入用户名"
                     :rules="[{ validator:checkUserName,  message: '用户名只能包含 字母、数字、下划线' }]" />
          <van-field v-model="upForm.email"
                     name="email"
                     label="邮箱"
                     placeholder="请输入邮箱"
                     :rules="[{ validator:checkEmail, message: '请填写正确的邮箱' }]" />
          <van-field v-model="upForm.mobile"
                     name="mobile"
                     label="手机号"
                     placeholder="请输入手机号"
                     :rules="[{ validator:checkMobile, message: '请填写正确的手机号' }]" />
          <div style="margin: 50px 16px 16px 16px;">
            <van-button round
                        block
                        type="info"
                        native-type="submit">提交</van-button>
          </div>
        </van-form>
      </div>
    </van-popup>
  </van-row>
</template>


<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import userInformation from "@/components/userInformation";
import { getDepartment } from "@/utils/user";
import { updateUserInfo } from "@/api/userApi";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    userInformation,
  },
  data () {
    //这里存放数据
    return {
      information: JSON.parse(localStorage.getItem("userInfo")),
      department: '',
      loading: false, // 列表加载状态
      finished: true, // 列表加载完成
      showPop: false, // 是否显示信息修改框
      upForm: {
        uid: JSON.parse(localStorage.getItem("userInfo")).uid,
        mobile: JSON.parse(localStorage.getItem("userInfo")).mobile,
        real_name: JSON.parse(localStorage.getItem("userInfo")).real_name,
        username: JSON.parse(localStorage.getItem("userInfo")).username,
        email: JSON.parse(localStorage.getItem("userInfo")).email
      }
    }
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    getDepartment(data => {
      this.department = data.name
    })
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 编辑个人信息
    updateInfo () {
      if (!(this.upForm.username || this.upForm.email || this.upForm.mobile)) {
        this.$toast("用户名,邮箱,手机号,请至少填写一项")
        return
      }
      this.$dialog
        .confirm({
          message: '是否修改个人信息',
        }).then(() => {
          updateUserInfo(this.upForm).then(res => {
            console.log("修改返回", res);
            localStorage.setItem("userInfo", JSON.stringify(res.data))
            this.information = res.data
            this.$toast("修改成功")
            this.showPop = false
          })
        }).catch()
    },


    checkInfo () {
      if (this.upForm.username || this.upForm.email || this.upForm.mobile) {
        return true
      } else {
        return false
      }
    },
    checkUserName (val) {
      if (val) {
        const myreg = /^[a-zA-Z0-9_]+$/;
        if (!myreg.test(val)) {
          return false;
        }
        else {
          return true;
        }
      } else {
        return true
      }
    },
    checkEmail (val) {
      if (val) {
        const myreg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
        if (!myreg.test(val)) {
          return false;
        }
        else {
          return true;
        }
      } else {
        return true
      }

    },
    checkMobile (val) {
      if (val) {
        const myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
        if (!myreg.test(val)) {
          return false;
        } else {
          return true;
        }
      } else {
        return true
      }
    },

    // 返回首页
    Home () {
      this.$router.push({ name: 'Home' })
    },
    perCenter () {
      this.$router.push({ name: 'PersonalCenter' })
    },
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  }


}
</script>


<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../../styles/dimens';
//蓝色框部分
.dt-main {
  height: 100%;
}
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: 'SourceHanSansCN-Medium';
  color: #ecedf2;
}
.dt-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: calc(100% - #{$headerHeight} - #{$statusBarHeight});
}

.pop-box {
  height: 100%;
  padding-top: px2rem(50);
}
</style>