<!-- 头像 -->
<template>
  <div :class="sizeClass">{{username | filtName}}</div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  props: ["username", "size"],
  filters: {
    filtName (name) {
      if (!name) {
        return '员工'
      }
      if (name.length > 2) {
        return name.slice(name.length - 2, name.length)
      } else {
        return name
      }
    }
  },
  data () {
    //这里存放数据
    return {
      sizeClass: ''
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    if (this.size == "large") {
      this.sizeClass = "dt-headPortrait-large"
    } else if (this.size == "small") {
      this.sizeClass = "dt-headPortrait-small"
    } else {
      this.sizeClass = "dt-headPortrait-small"
    }
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {

  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {

  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import "../../styles/dimens";
.dt-headPortrait-small {
  height: px2rem(35);
  width: px2rem(35);
  background-color: #5870f8;
  border-radius: px2rem(5);
  line-height: px2rem(35);
  text-align: center;
  font-size: px2rem(10);
  color: #ffffff;
}
.dt-headPortrait-large {
  height: px2rem(50);
  width: px2rem(50);
  background-color: #5870f8;
  border-radius: px2rem(5);
  line-height: px2rem(50);
  text-align: center;
  font-size: px2rem(15);
  font-weight: 500;
  color: #ffffff;
}
</style>